@import 'scss';

.TextInput {
	@extend %shadow;
	@extend %focusShadow;
	@extend %padding;
	background: v(back2);
	border: none;
	outline: none;

	color: v(text2);

	&::placeholder {
		color: v(text1);
	}
}

.TextInput,
.TextInputWrap {
	@extend %bradius;
	width: 100%;
}

.TextInput.Transparent {
	box-shadow: none;
	background: none;
}
