// Assort names of all theme variables in the following map.
$theme-vars: (
	font-sizes: [ font-s font-m font-l ],
	background-colors: [ back1 back2 ],
	text-colors: [ text1 text2 ],
	ui-colors: [ ui1 shdclr ],
);

// In app root: "@extend %theme-root;"
%themeRoot {
	// Define default values for of theme variables.
	--font-s: 14px;
	--font-m: 20px;
	--font-l: 32px;

	--back1: #f0f0f0;
	--back2: #ffffff;

	--text1: #929292;
	--text2: #1b1c1d;

	--ui1: #668fff;
	--shdclr: #22222231;
}

/* 
	Use @function v($var) to have passed variable names validated and wrapped with the native css var() function:
	"color: v(clr1);" --> "color: var(--clr1);"
*/
@function v($var) {
	$var-is-invalid: true;
	@each $list, $vars in $theme-vars {
		@if (index($vars, $var) != null) {
			$var-is-invalid: false;
		}
	}

	@if ($var-is-invalid) {
		@error "Wrong $var: #{$var} in v($var)";
	}

	@return var(--#{$var});
}
