@import 'scss';

.DraggableInput {
	@extend %bradius;
	@extend %shadow;
	@extend %focusShadow;
	margin: 0 0 20px 0;
	background: v(back2);

	display: flex;
	align-items: center;
}
.InputDragHandle {
	margin-left: 20px;
	width: 20px;
}
.DraggableInput.Headline {
	box-shadow: none;
	background: none;
}
