@import 'scss';

.Form {
	min-width: 450px;
	max-width: 100%;
	min-height: 300px;

	height: 100%;
	display: flex;
	flex-direction: column;
}
.FormToolbar {
	background: v(back2);
	border-bottom: solid 1px v(text1);
	padding: 20px;
	width: 100%;
	overflow: auto;

	display: flex;
	align-items: center;

	& > *:not(:last-child) {
		margin: 0 20px 0 0;
	}
}
.FormPreviewToggleWrap {
	@extend %bradius;
	@extend %padding;

	display: flex;
	align-items: center;
}
.Toggle.FormPreviewToggle {
	margin-left: 15px;
}
.ImportInput {
	@extend %padding;
	@extend %bradius;
	@extend %shadow;
	@extend %focusShadow;
	background: v(back2);

	display: flex;
	align-items: center;

	input {
		margin: 0 0 0 20px;
	}
}

.FormCenter {
	overflow: auto;
	min-height: 0;
	flex: 1;

	// Center content
	display: grid;
	& > div {
		margin: auto;

		width: 100%;
		display: flex;
		justify-content: space-evenly;
		align-items: center;
	}

	// Fit in remaining height
	min-height: 0;
}

.Tooltips {
	color: v(text1);
	white-space: pre-wrap;
}
