@import 'scss';

.Toggle {
	margin: 5px;
	padding: 0 1px 0 0;
	border: 0;
	border-radius: 12px;
	height: 12px;
	width: 23px;
	outline: none;
	cursor: pointer;
	background: v(ui1);
	transition: background-color 0.2s;

	position: relative;
	&Bar {
		position: absolute;
		right: -1px;
		top: 0;

		border-radius: 12px;
		height: 12px;
		width: 24px;
		background: v(back1);

		transition: width 0.2s, background-color 0.2s;
		.Toggled > & {
			width: 12px;
		}

		display: flex;
		align-items: center;
	}

	&Point {
		flex: 0 0 16px;
		position: relative;
		right: 2px;
		height: 16px;

		border-radius: 16px;
		box-shadow: 0 0 1px 2px #0000001c;
		background: v(back2);
		transition: box-shadow 0.2s ease, background-color 0.2s;
	}

	&:hover,
	&:focus {
		.TogglePoint {
			box-shadow: 0 0 1px 4px #0000001c;
		}
	}
}
